import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hey! I'm <b className="purple">Kaia Burgos.</b>
            <br />
            <br />
            I am an
            <b className="purple"> empathy-driven, innovation-minded </b>
            individual who strives to bridge technology and design in order to create 
            usable solutions for all people. 
            <br />
            <br />
            Currently, I study 
            <b className="purple"> Electrical and Computer Engineering </b>
            at the University of Washington in rainy Seattle. ☔
            <br />
            <br />
            Professionally, my interests include:
          </p>
          <ul>
            <li className="about-activity">
              ✦ Accessible AI
            </li>
            <li className="about-activity">
              ✦ Data Ethics
            </li>
            <li className="about-activity">
              ✦ Human-Centered Technology
            </li>
          </ul>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
