import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import Particle from "../Particle";
import Aboutcard from "./AboutCard";
import kaia from "../../Assets/kaia.png";

function About() {
  return (
    <Container fluid className="about-section">
      <Particle />
      <Container>
        <Row style={{ justifyContent: "center", padding: "10px" }}>
          <Col
            md={7}
            style={{
              justifyContent: "center",
              paddingTop: "30px",
              paddingBottom: "50px",
            }}
          >
            <h1 style={{ fontSize: "2.1em", paddingBottom: "20px" }}>
            ⭒❃.✮:▹ about me ◃:✮.❃⭒
            </h1>
            <Aboutcard />
          </Col>
          <Col
            md={5}
            style={{ paddingTop: "120px", paddingBottom: "50px" }}
            className="about-img"
          >
            <img src={kaia} alt="about" className="img-fluid" />
          </Col>
        </Row>
      <Container>
        <Row style={{ justifyContent: "center", paddingTop: "10px", paddingBottom: "50px" }}>
          <blockquote className="blockquote mb-0">
            <p style={{ textAlign: "justify" }}>
              What draws me to engineering is the ability to harness creativity in the digital space,
              by means of technical problem-solving and scientific application.
              I aspire to bring ideas to life, and to enable others to do the same.
            <br />
            <br />
              I've obtained a wide breadth of experience throughout the tech stack - from RTL/embedded systems
              design up to building neural networks in Python. I believe that thorough contextual knowledge
              can make a good engineer a great one - and such is what I aspire to be. 
            <br />
            <br />
              Outside of work, you'll probably find me playing sports, enjoying music, strolling
              in a local market, or enjoying nature. I will likely be found with coffee.
            </p>
          </blockquote>
        </Row>
      </Container>
      </Container>
      <p className="purple">
        let's chat over coffee!
        <br />
        kaiaburgos02@gmail.com
      </p>
    </Container>
  );
}

export default About;
